@import "components/header";
@import "components/aside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    min-height: 1800px;
    .container{
      .row{
        .col-lg-9{
          h5{
            margin: 35px 40px;
            font: 1.25em/1.375em "Arial";
            font-weight: bold;
            text-transform: uppercase;
          }
          .item{
            padding: 25px 0;
            display: flex;
            justify-content: space-between;
            border-top: 1px solid #c7c7c7;
            &__img{
              margin: 0 0 0 40px;
              width: 10%;
              img{
                width: 100%;
              }
            }
            &__info{
              width: 82.5%;
              &__headline{
                span{
                  font: .973em/1.063em "Arial";
                  color: #616161;
                }
                h6{
                  font: 1em/1.125em "Arial";
                  font-weight: bold;
                }
              }
              &__parametrs{
                margin: 20px 0 30px;
                ul{
                  display: flex;
                  li{
                    display: flex;
                    flex-direction: column;
                    span{
                      font: .875em/1.75em "Arial";
                      color: #525252;
                    }
                    &:nth-child(2){
                      margin: 0 0 0 10px;
                      span{
                        font-weight: bold;
                      }
                      #paymentState{
                        color: #e06565;
                      }
                    }
                  }
                }
              }
              &__actions{
                a{
                  margin: 0 50px 0 0;
                  display: inline-block;
                  font: 1em/1.125em "Arial";
                  color: #009fff;
                  text-decoration: underline;
                }
              }
            }
            &:last-child{
              border-bottom: 1px solid #c7c7c7;
            }
          }
        }
        .col-lg-3{
          .user{
            margin: 30px 0 0;
            &__name{
              span{
                font: .875em/1.25em "Arial";
                font-style: italic;
                color: #636363;
              }
              p{
                font: 1em/1.125em "Arial";
                font-weight: bold;
                text-transform: uppercase;
              }
            }
            &__foto{
              margin: 25px 0;
              img{
                width: 100%;
                min-height: 265px;
              }
            }
            &__data{
              p{
                font: 1em/2em "Arial";
              }
            }
            &__parametrs{
              display: flex;
              flex-direction: column;
              a{
                margin: 10px 0;
                width: 100%;
                display: block;
                font: 1em/45px "Arial";
                color: #636363;
                text-align: center;
                text-decoration: none;
                background-color: #eee;
                border: 1px solid #dedede
              }
            }
          }
        }
      }
    }
  }
}